'use client';

import { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { apiGet, apiPatch } from '../utils/api.utils';

/**
 * ═══════════════════════════════════════════════════════════════════════
 * AUTH CONTRACT - DO NOT MODIFY WITHOUT REVIEW
 * ═══════════════════════════════════════════════════════════════════════
 * 
 * TOKEN SOURCE: localStorage ONLY
 * - Key name: 'auth_token'
 * - No cookies, no sessionStorage, no alternatives
 * 
 * LOGOUT: Must go through AuthContext.sign_out() ONLY
 * - No component may clear localStorage directly
 * - No duplicate logout logic allowed
 * 
 * TOKEN ACCESS: No component may read token directly
 * - Use useAuth() hook to access auth state
 * - api.utils.ts is the only file that reads localStorage.getItem('auth_token')
 * 
 * REHYDRATION: Backend verification on app boot
 * - Calls GET /api/customers/me to verify token
 * - 401 responses trigger automatic logout via global handler
 * 
 * ═══════════════════════════════════════════════════════════════════════
 */

export interface User {
    id: string;
    name: string;
    email: string;
    phone?: string;
    profile_pic?: string;
    addresses?: any[];
    role?: 'customer' | 'vendor' | 'admin';
    ui_preferences?: {
        theme?: 'light' | 'dark';
    };
}

interface AuthContextType {
    user: User | null;
    token: string | null;
    role: 'customer' | 'vendor' | 'admin' | null;
    is_authenticated: boolean;
    loading: boolean;
    sign_in: (user: User, token: string) => void;
    sign_out: () => void;
    update_theme: (theme: 'light' | 'dark') => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
    const [user, set_user] = useState<User | null>(null);
    const [token, set_token] = useState<string | null>(null);
    const [role, set_role] = useState<'customer' | 'vendor' | 'admin' | null>(null);
    const [loading, set_loading] = useState(true);

    // Apply theme helper
    const apply_theme = (theme: 'light' | 'dark') => {
        document.body.classList.remove('theme-light', 'theme-dark');
        document.body.classList.add(`theme-${theme}`);
        localStorage.setItem('cc_theme', theme);
    };

    // HARD AUTH REHYDRATION: Verify token with backend on boot
    useEffect(() => {
        const rehydrateAuth = async () => {
            const stored_token = localStorage.getItem('auth_token');

            // No token = logged out (but don't redirect - just mark as ready)
            if (!stored_token) {
                set_loading(false);
                if (typeof window !== 'undefined') {
                    (window as any).__AUTH_READY__ = true;
                }
                return;
            }

            // Determine role from stored user data to call correct /me endpoint
            let stored_role: string | null = null;
            try {
                const stored_user = localStorage.getItem('auth_user');
                if (stored_user) {
                    const parsed_user = JSON.parse(stored_user);
                    stored_role = parsed_user.role;
                }
            } catch {
                // If we can't parse stored user, clear data but DON'T redirect
                localStorage.removeItem('auth_token');
                localStorage.removeItem('auth_user');
                set_loading(false);
                if (typeof window !== 'undefined') {
                    (window as any).__AUTH_READY__ = true;
                }
                return;
            }

            // Token exists - verify with backend using correct endpoint
            try {
                // Call correct /me endpoint based on role
                const endpoint = (stored_role === 'vendor' || stored_role === 'admin')
                    ? '/api/vendors/me'
                    : '/api/customers/me';

                const { data, error } = await apiGet(endpoint);

                if (error || !data?.success) {
                    // Token invalid/expired - clear state but DON'T redirect
                    // The global 401 handler in api.utils will handle redirect
                    set_user(null);
                    set_token(null);
                    set_role(null);
                } else {
                    // Token valid - hydrate state
                    const user_data = data.user || data.data?.user;
                    set_user(user_data);
                    set_token(stored_token);
                    set_role(user_data.role || 'customer');

                    // Store user data for quick access
                    localStorage.setItem('auth_user', JSON.stringify(user_data));

                    // Apply theme
                    if (user_data.ui_preferences?.theme) {
                        apply_theme(user_data.ui_preferences.theme);
                    }
                }
            } catch (err) {
                // Network error or unexpected failure - clear state but DON'T redirect
                console.error('Auth rehydration failed:', err);
                set_user(null);
                set_token(null);
                set_role(null);
            } finally {
                set_loading(false);
                // Mark auth as ready - redirects are now allowed
                if (typeof window !== 'undefined') {
                    (window as any).__AUTH_READY__ = true;
                }
            }
        };

        rehydrateAuth();
    }, []);

    const sign_in = (user_data: User, token: string) => {
        localStorage.setItem('auth_token', token);
        localStorage.setItem('auth_user', JSON.stringify(user_data));
        set_user(user_data);
        set_token(token);
        set_role(user_data.role || 'customer');

        // Apply theme from user data on login
        if (user_data.ui_preferences?.theme) {
            apply_theme(user_data.ui_preferences.theme);
        }
    };

    const update_theme = async (theme: 'light' | 'dark') => {
        // 1. Optimistic Update
        apply_theme(theme);

        // 2. Update Context state if user exists
        if (user) {
            const updated_user = {
                ...user,
                ui_preferences: { ...user.ui_preferences, theme }
            };
            set_user(updated_user);
            localStorage.setItem('auth_user', JSON.stringify(updated_user));

            // 3. Sync with Backend using centralized API
            if (token) {
                try {
                    await apiPatch('/api/customers/ui-preferences', { theme });
                } catch (error) {
                    console.error("Failed to sync theme preference", error);
                }
            }
        }
    };

    const sign_out = () => {
        // Read role from current state or fallback to localStorage
        const userRole = role || (() => {
            try {
                const stored = localStorage.getItem('auth_user');
                if (stored) {
                    return JSON.parse(stored).role;
                }
            } catch { }
            return null;
        })();

        // Clear auth state and storage
        localStorage.removeItem('auth_token');
        localStorage.removeItem('auth_user');
        set_user(null);
        set_token(null);
        set_role(null);

        // Use role-aware redirect
        const { getLoginRedirectPath } = require('../utils/authRedirect.utils');
        const redirectPath = getLoginRedirectPath(userRole);
        window.location.href = redirectPath;
    };

    return (
        <AuthContext.Provider
            value={{
                user,
                token,
                role,
                is_authenticated: Boolean(token && user),
                loading,
                sign_in,
                sign_out,
                update_theme
            }}
        >
            {children}
        </AuthContext.Provider>
    );
}

export const useAuth = () => {
    const ctx = useContext(AuthContext);
    if (!ctx) throw new Error('useAuth must be used within AuthProvider');
    return ctx;
};
