import { ApiResponse } from '../types/auth.types';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000';

/**
 * ═══════════════════════════════════════════════════════════════════════
 * API CONTRACT - DO NOT MODIFY WITHOUT REVIEW
 * ═══════════════════════════════════════════════════════════════════════
 * 
 * CENTRALIZED API GATEWAY: All authenticated API calls MUST go through this file
 * - Use apiCall, apiGet, apiPost, apiPatch, apiPut, apiDelete
 * - NO direct fetch() calls in components/pages for authenticated requests
 * 
 * GLOBAL 401 HANDLING: Automatic logout on authentication failure
 * - Any 401 response clears localStorage and redirects to login
 * - NO page-level auth error handling allowed
 * - Prevents stuck auth states and retry loops
 * 
 * TOKEN ATTACHMENT: Automatic from localStorage
 * - Reads 'auth_token' from localStorage
 * - Attaches as 'Authorization: Bearer <token>' header
 * - Only this file and AuthContext may read auth_token
 * 
 * ═══════════════════════════════════════════════════════════════════════
 */
export async function apiCall<T = any>(
    endpoint: string,
    options: RequestInit = {}
): Promise<{ data?: ApiResponse<T>; error?: string; errorCode?: string }> {
    try {
        // JWT Authentication: Attached from localStorage
        const token = typeof window !== 'undefined' ? localStorage.getItem('auth_token') : null;
        const authHeader = token ? { 'Authorization': `Bearer ${token}` } : {};

        const response = await fetch(`${API_BASE_URL}${endpoint}`, {
            ...options,
            headers: {
                'Content-Type': 'application/json',
                ...authHeader,
                ...options.headers,
            },
        });

        // GLOBAL 401 HANDLER: Force logout on authentication failure
        if (response.status === 401) {
            if (typeof window !== 'undefined') {
                // Read role from stored user data
                let userRole: string | null = null;
                try {
                    const storedUser = localStorage.getItem('auth_user');
                    if (storedUser) {
                        const user = JSON.parse(storedUser);
                        userRole = user.role;
                    }
                } catch {
                    // Ignore parse errors
                }

                // Clear all auth data BEFORE redirect
                localStorage.removeItem('auth_token');
                localStorage.removeItem('auth_user');

                // ONLY redirect if auth is ready (prevents redirect during initial load)
                const authReady = (window as any).__AUTH_READY__ === true;

                if (authReady) {
                    // Import redirect resolver dynamically to avoid circular deps
                    const { getLoginRedirectPath } = await import('./authRedirect.utils');
                    const redirectPath = getLoginRedirectPath(userRole);

                    // Redirect exactly once (prevent infinite loops)
                    if (!window.location.pathname.includes('/signin') &&
                        !window.location.pathname.includes('/signup') &&
                        !window.location.pathname.includes('/login')) {
                        window.location.href = redirectPath;
                    }
                }
            }

            return {
                error: 'Session expired. Please login again.',
                errorCode: 'UNAUTHORIZED',
            };
        }

        const data: ApiResponse<T> = await response.json();

        if (!response.ok) {
            return {
                error: data.message || 'Request failed',
                errorCode: data.error_code,
            };
        }

        return { data };
    } catch (error) {
        return {
            error: error instanceof Error ? error.message : 'Network error',
            errorCode: 'NETWORK_ERROR',
        };
    }
}

/**
 * POST request helper
 */
export async function apiPost<T = any>(
    endpoint: string,
    body: any
): Promise<{ data?: ApiResponse<T>; error?: string; errorCode?: string }> {
    return apiCall<T>(endpoint, {
        method: 'POST',
        body: JSON.stringify(body),
    });
}

/**
 * GET request helper
 */
export async function apiGet<T = any>(
    endpoint: string
): Promise<{ data?: ApiResponse<T>; error?: string; errorCode?: string }> {
    return apiCall<T>(endpoint, {
        method: 'GET',
    });
}

